import React from 'react';
import './App.css';

interface Element {
    name: string;
    imageLink: string;
}

interface AppState {
    spellBar: Element[];
    maxElements: number;
    usePoison: boolean;
}

const elements = [
    'arcane',
    'cold',
    'earth',
    'fire',
    'ice',
    'life',
    'lightning',
    'poison',
    'shield',
    'steam',
    'water',
].map((elementName) => {
    return {
        name: elementName,
        imageLink: `images/Element_${elementName}.webp`
    }
});

const [arcane, cold, earth, fire, ice, life, lightning, poison, shield, steam, water] = [...elements];

const opposingElements = {
    [arcane.name]: [life],
    [life.name]: [arcane],
    [water.name]: [lightning],
    [shield.name]: [shield],
    [cold.name]: [fire],
    [lightning.name]: [water, earth],
    [earth.name]: [lightning],
    [fire.name]: [cold]
};

const cancelingSubElements = {
    [fire.name]: [[ice, fire],],
    [life.name]: [[poison, water]],
    [poison.name]: [[life, water]],
    [ice.name]: [[fire, water]],
    [steam.name]: [[cold, water]],
}

const combiningElements = {
    [water.name]: [[fire, steam], [cold, ice], [arcane, poison]],
    [fire.name]: [[water, steam],],
    [cold.name]: [[steam, water], [water, ice]],
    [arcane.name]: [[water, poison]],
};



class App extends React.Component<{}, AppState> {

    constructor(props: {}) {
        super(props);
        this.state = {
            spellBar: [],
            maxElements: 5,
            usePoison: false,
        };

        document.addEventListener('keypress', (event) => {
            switch (event.key.toLowerCase()) {
                case 'q':
                    this.addToSpellBar(water);
                    break;
                case 'w':
                    this.addToSpellBar(life);
                    break;
                case 'e':
                    this.addToSpellBar(shield);
                    break;
                case 'r':
                    this.addToSpellBar(cold);
                    break;
                case 'a':
                    this.addToSpellBar(lightning);
                    break;
                case 's':
                    this.addToSpellBar(arcane);
                    break;
                case 'd':
                    this.addToSpellBar(earth);
                    break;
                case 'f':
                    this.addToSpellBar(fire);
                    break;
                default:
                    break;
            }
        })
    }

    checkOpposing(e0: Element, e1: Element): boolean {
        if (opposingElements[e0.name] !== undefined) {
            for (const opposingElement of opposingElements[e0.name]) {
                if (opposingElement === e1) {
                    return true;
                }
            }
        }

        return false;
    }

    addToSpellBar(element: Element) {

        const spellBar = this.state.spellBar;

        if (spellBar.length > 0) {

            for (let i = spellBar.length - 1; i >= 0; i--) {
                if (this.checkOpposing(spellBar[i], element)) {
                    this.setState({
                        spellBar: [...spellBar.slice(0, i), ...spellBar.slice(i + 1, spellBar.length)]
                    });

                    return;
                }
            }

            for (let i = spellBar.length - 1; i >= 0; i--) {

                if (cancelingSubElements[spellBar[i].name] !== undefined) {
                    for (const [combiningElement, combinedElement] of cancelingSubElements[spellBar[i].name]) {

                        if (combiningElement === element) {

                            let newSpellBar = [...spellBar.slice(0, i), combinedElement, ...spellBar.slice(i + 1, spellBar.length)];

                            let indexLeft = i;
                            let indexRight = i;

                            while (indexLeft >= 0 || indexRight <= this.state.maxElements - 1) {
                                indexLeft--;
                                if (indexLeft >= 0) {
                                    if (this.checkOpposing(newSpellBar[indexLeft], combinedElement)) {
                                        this.setState({
                                            spellBar: [...newSpellBar.slice(0, indexLeft), ...newSpellBar.slice(indexLeft + 1, i), ...newSpellBar.slice(i + 1, newSpellBar.length)]
                                        });

                                        return;
                                    }
                                }

                                indexRight++;
                                if (indexRight < newSpellBar.length) {
                                    if (this.checkOpposing(newSpellBar[indexRight], combinedElement)) {
                                        this.setState({
                                            spellBar: [...newSpellBar.slice(0, i), ...newSpellBar.slice(i + 1, indexRight), ...newSpellBar.slice(indexRight + 1, newSpellBar.length)]
                                        });

                                        return;
                                    }
                                }
                            }

                            this.setState({
                                spellBar: newSpellBar
                            });

                            return;
                        }
                    }
                }
            }

            for (let i = spellBar.length - 1; i >= 0; i--) {
                if (combiningElements[spellBar[i].name] !== undefined) {
                    for (const [combiningElement, combinedElement] of combiningElements[spellBar[i].name]) {
                        if (combiningElement === element) {

                            if (!this.state.usePoison && combinedElement === poison) {
                                continue;
                            }

                            this.setState({
                                spellBar: [...spellBar.slice(0, i), combinedElement, ...spellBar.slice(i + 1, spellBar.length)]
                            });

                            return;
                        }
                    }
                }
            }
        }

        if (this.state.spellBar.length < this.state.maxElements) {
            this.setState({
                spellBar: [...spellBar, element]
            });
        }
    }

    countElement(element: Element): number {
        let output = 0;

        for (const barElement of this.state.spellBar) {
            if (element === barElement) {
                output++;
            }
        }

        return output;
    }

    get shieldCount(): number {
        return this.countElement(shield);
    }

    get earthCount(): number {
        return this.countElement(earth);
    }

    get iceCount(): number {
        return this.countElement(ice);
    }

    get arcaneCount(): number {
        return this.countElement(arcane);
    }

    get lifeCount(): number {
        return this.countElement(life);
    }

    get steamCount(): number {
        return this.countElement(steam);
    }

    get lightningCount(): number {
        return this.countElement(lightning);
    }

    get waterCount(): number {
        return this.countElement(water);
    }

    get fireCount(): number {
        return this.countElement(fire);
    }

    get coldColunt(): number {
        return this.countElement(cold);
    }

    get poisonCount(): number {
        return this.countElement(poison);
    }

    getElementNames(elements: Element[]): string {
        return elements.map((element) => {
            return `${element.name[0].toUpperCase()}${element.name.slice(1)}`;
        }).join(' ');
    }

    render() {

        let forward: JSX.Element = <span></span>;
        let area: JSX.Element = <span></span>;
        let weapon: JSX.Element = <span></span>;
        let self: JSX.Element = <span></span>;

        let elementsUsedSet = new Set<Element>();

        for (let element of this.state.spellBar) {
            elementsUsedSet.add(element);
        }

        let elementsUsed = Array.from(elementsUsedSet).sort((a, b) => {
            if (a.name < b.name) {
                return -1;
            } else if (a.name > b.name) {
                return 1;
            } else {
                return 0;
            }
        });


        if (this.state.spellBar.length === 0) {

            // No Elements

            const func = (areaCast: boolean) => {
                return <div>
                    <b style={{ fontSize: 'larger' }}>Wind Burst</b><br />
                    <b>Casting Time:</b> See text<br />
                    <b>Range:</b> {areaCast ? 'Centered on you, see text' : 'See text'}<br />
                    <b>Area:</b> {areaCast ? 'Radial Burst' : 'Cone Shaped Burst'}, see text<br />
                    <b>Duration:</b> Instantaneous<br />
                    <b>Saving Throw:</b> Fortitude Negates (object)<br />
                    <br />
                    Creates a blast of air that originates from you, affecting all creatures in its path. If cast as a full round action, the wind force is one category stronger and has double the range. If cast as a swift action, the wind force is one category weaker and has half the range.
                    <br /><br />
                    <table>
                        <tr><th>Caster Level</th><th>Default Wind Force</th><th>Range</th></tr>
                        <tr><td>1</td><td>Strong</td><td>{areaCast ? 15: 30} ft</td></tr>
                        <tr><td>5</td><td>Severe</td><td>{areaCast ? 30: 60} ft</td></tr>
                        <tr><td>10</td><td>Windstorm</td><td>{areaCast ? 60: 120} ft</td></tr>
                        <tr><td>15</td><td>Hurricane</td><td>{areaCast ? 120: 240} ft</td></tr>
                    </table>
                </div>
            }

            forward = (
                func(false)
            );

            area = (
                func(true)
            );
        } else if (this.state.spellBar.length === 1 && this.state.spellBar[0] === shield) {

            // Just Shield

            forward = (
                <div>
                    <h4>Arc Shield</h4>
                </div>
            );

            area = (
                <div>
                    <h4>Sphere Shield</h4>
                </div>
            );

            weapon = (
                <div>
                    <h4>Wall Shield</h4>
                </div>
            );

            self = (
                <div>
                    <h4>Force Armor</h4>
                </div>
            );
        } else if (this.shieldCount === 1) {

            // Shield

            let elementsExceptShield = [...elementsUsed];
            elementsExceptShield.splice(elementsExceptShield.findIndex(element => element === shield), 1);

            self = (
                <div>
                    <h4>{this.getElementNames(elementsExceptShield)} Armor</h4>
                </div>
            );

            if (this.earthCount >= 1) {

                let elementsExceptShieldAndEarth = [...elementsExceptShield];
                elementsExceptShieldAndEarth.splice(elementsExceptShieldAndEarth.findIndex(element => element === earth), 1);

                // Shield + Earth

                forward = (
                    <div>
                        <h4>Arc {this.getElementNames(elementsExceptShieldAndEarth)} Earth Wall</h4>
                    </div>
                );

                area = (
                    <div>
                        <h4>Circular {this.getElementNames(elementsExceptShieldAndEarth)} Earth Wall</h4>
                    </div>
                );

                weapon = (
                    <div>
                        <h4>Straight {this.getElementNames(elementsExceptShieldAndEarth)} Earth Wall</h4>
                    </div>
                );
            } else if (this.iceCount >= 1) {

                let elementsExceptShieldAndIce = [...elementsExceptShield];
                elementsExceptShieldAndIce.splice(elementsExceptShieldAndIce.findIndex(element => element === ice), 1);

                // Shield + Ice

                forward = (
                    <div>
                        <h4>Arc {this.getElementNames(elementsExceptShieldAndIce)} Ice Wall</h4>
                    </div>
                );

                area = (
                    <div>
                        <h4>Circular {this.getElementNames(elementsExceptShieldAndIce)} Ice Wall</h4>
                    </div>
                );

                weapon = (
                    <div>
                        <h4>Straight {this.getElementNames(elementsExceptShieldAndIce)} Ice Wall</h4>
                    </div>
                );
            } else if (this.arcaneCount >= 1 || this.lifeCount >= 1) {

                // Mines

                forward = (
                    <div>
                        <h4>Arc {this.getElementNames(elementsExceptShield)} Mines</h4>
                    </div>
                );

                area = (
                    <div>
                        <h4>Circular Surrounding {this.getElementNames(elementsExceptShield)} Mines</h4>
                    </div>
                );

                weapon = (
                    <div>
                        <h4>Straight Line {this.getElementNames(elementsExceptShield)} Mines</h4>
                    </div>
                );
            } else {

                // Spray Wall

                forward = (
                    <div>
                        <h4>Arc {this.getElementNames(elementsExceptShield)} Wall</h4>
                    </div>
                );

                area = (
                    <div>
                        <h4>Circular Surrounding {this.getElementNames(elementsExceptShield)} Wall</h4>
                    </div>
                );

                weapon = (
                    <div>
                        <h4>Straight Line {this.getElementNames(elementsExceptShield)} Wall</h4>
                    </div>
                );

            }

        } else if (this.earthCount >= 1) {

            // Projectile

            forward = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Projectile</h4>
                </div>
            );

            area = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Quake</h4>
                </div>
            );

            weapon = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Fissure</h4>
                </div>
            );

            self = (
                <div>
                    <h4>Drop {this.getElementNames(elementsUsed)} on head</h4>
                </div>
            );

        } else if (this.iceCount >= 1) {

            // Barrage

            forward = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Barrage</h4>
                </div>
            );

            area = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Spikes</h4>
                </div>
            );

            weapon = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Blade</h4>
                </div>
            );

            self = (
                <div>
                    <h4>Drop {this.getElementNames(elementsUsed)} on head</h4>
                </div>
            );
        } else if (this.arcaneCount >= 1 || this.lifeCount >= 1) {

            // Beam

            forward = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Beam</h4>
                </div>
            );

            area = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Burst</h4>
                </div>
            );

            weapon = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Blade</h4>
                </div>
            );

            self = (
                <div>
                    <h4>Use {this.getElementNames(elementsUsed)} on self</h4>
                </div>
            );

        } else if (this.lightningCount >= 1 || this.steamCount === 0) {

            // Lightning

            let elementsExceptLightning = [...elementsUsed];
            elementsExceptLightning.splice(elementsExceptLightning.findIndex(element => element === lightning), 1);

            forward = (
                <div>
                    <h4>{this.getElementNames(elementsExceptLightning)} Lightning</h4>
                </div>
            );

            area = (
                <div>
                    <h4>{this.getElementNames(elementsExceptLightning)} Area Lightning</h4>
                </div>
            );

            weapon = (
                <div>
                    <h4>{this.getElementNames(elementsExceptLightning)} Blade</h4>
                </div>
            );

            self = (
                <div>
                    <h4>Use {this.getElementNames(elementsUsed)} on Self</h4>
                </div>
            );
        } else {

            // Spray

            forward = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Spray</h4>
                </div>
            );

            area = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Area Spray</h4>
                </div>
            );

            weapon = (
                <div>
                    <h4>{this.getElementNames(elementsUsed)} Blade</h4>
                </div>
            );

            self = (
                <div>
                    <h4>Use {this.getElementNames(elementsUsed)} on Self</h4>
                </div>
            );
        }

        return (
            <div style={{ display: 'flex' }}>
                <div style={{ paddingLeft: '5px', paddingRight: '5px', flexShrink: 0 }}>
                    <div style={{ height: '200px' }}>
                        Max Elements: <input
                            type='number'
                            style={{ width: '50px' }}
                            value={this.state.maxElements}
                            onChange={(event) => {
                                this.setState((state) => {
                                    return {
                                        maxElements: +event.target.value,
                                        spellBar: state.spellBar.slice(0, +event.target.value)
                                    }
                                });
                            }}
                        ></input><br />
                        <input
                            type='checkbox'
                            checked={this.state.usePoison}
                            onChange={(event) => {
                                this.setState((state) => {
                                    return {
                                        usePoison: event.target.checked,
                                        spellBar: []
                                    }
                                });
                            }}
                        ></input> Use Poison
                        <h3>Spell Bar</h3>
                        <div style={{ height: '50px' }}>
                            {this.state.spellBar.map((element) => <img src={`${element.imageLink}`} alt={`${element.name}`}></img>)}
                        </div>
                        <button onClick={() => { this.setState({ spellBar: [] }) }}>Clear</button>
                    </div>
                    <br />
                    <br />
                    <br />
                    <div>
                        {[water, life, shield, cold].slice(0, 4).map((element) => (
                            <img
                                style={{ cursor: 'pointer' }}
                                src={`${element.imageLink}`}
                                onClick={(event) => {
                                    this.addToSpellBar(element);
                                }}
                                alt={`${element.name}`}
                            >
                            </img>))}
                        QWER
                        <br />
                        {[lightning, arcane, earth, fire].slice(0, 4).map((element) => (
                            <img
                                style={{ cursor: 'pointer' }}
                                src={`${element.imageLink}`}
                                onClick={(event) => {
                                    this.addToSpellBar(element);
                                }}
                                alt={`${element.name}`}
                            >
                            </img>))}
                        ASDF
                        <br />
                        <br />
                        <img src='images/Element_steam.webp' alt='steam'></img> = <img src='images/Element_fire.webp' alt='fireImage'></img> + <img src='images/Element_water.webp' alt='water'></img> <br />
                        <img src='images/Element_ice.webp' alt='ice'></img> = <img src='images/Element_cold.webp' alt='coldImage'></img> + <img src='images/Element_water.webp' alt='water'></img> <br />
                        {this.state.usePoison ? <span><img src='images/Element_poison.webp' alt='poison'></img> = <img src='images/Element_arcane.webp' alt='arcane'></img> + <img src='images/Element_water.webp' alt='water'></img></span> : ''}

                    </div>
                </div>
                <div style={{ display: 'flex', fontSize: 'smaller' }}>
                    <div>
                        <div style={{ paddingLeft: '15px', paddingRight: '15px' }}>
                            <h2>Forward Cast</h2>
                            {forward}
                        </div>
                        <div style={{ paddingLeft: '15px', paddingRight: '15px' }}>
                            <h2>Area Cast</h2>
                            {area}
                        </div>
                    </div>
                    <div>
                        <div style={{ paddingLeft: '15px', paddingRight: '15px' }}>
                            <h2>Weapon Cast</h2>
                            {weapon}
                        </div>
                        <div style={{ paddingLeft: '15px', paddingRight: '15px' }}>
                            <h2>Self Cast</h2>
                            {self}
                        </div>
                    </div>
                </div>


            </div>
        );
    }
}

export default App;
